@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
  background-color: rgb(245, 244, 244);
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.App {
  max-width: 100%;
  width: 100%;
}
.material-icons {
  line-height: normal;
}

.Item{display:flex;justify-content:space-between;border:1px solid black;padding:10px;border-bottom:none;background-color:white}.Item:last-child{border-bottom:1px solid black}.Item-item{margin-right:10px;display:flex;align-items:center;font-size:24px}.operation{margin-left:10px;flex:1 1 auto}.price{margin-right:20px}.material-icons{font-size:24px;cursor:pointer}.edit{color:black}.edit:hover{color:#5959df}.del{font-size:28px}.del:hover{color:red}

.ListService{max-width:700px;width:100%;margin:0 auto;margin-top:30px;display:flex;flex-direction:column;justify-content:center;align-items:center}.ListService-row{width:100%;padding:10px;margin:0 auto}.loader{margin-top:20px;font-size:25px}.error{margin-top:20px;width:80%;padding:10px;background-color:#c24a4a;color:white;font-size:20px;text-align:center}

.EditService{max-width:400px;width:100%;margin:0 auto;padding:10px;padding-top:20px;position:relative;border-radius:10px;display:flex;flex-direction:column;justify-content:center;align-items:center}.EditService-Loading{position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.6);z-index:2;border-radius:10px;display:flex;justify-content:center;align-items:center}.EditService-Loading::before{content:"";display:block;width:100px;height:100px;background-repeat:no-repeat;background-position:center;background-size:contain;background-image:url(../../static/media/Preload.b2a8fa3d.gif);position:relative;z-index:3}.Editing-form-row{display:flex;justify-content:center;flex-direction:column}.EditService-lablel{display:flex;flex-direction:column;margin-bottom:20px}.EditService-control{display:flex}.form-item{margin-top:6px;border:1px solid black;padding:7px}.Item-row{margin-top:30px}.control{background-color:#db7070;color:white;margin-right:20px;padding-left:20px;padding-right:20px;border:none;border-radius:7px;font-size:18px;font-family:Roboto, sans-serif;line-height:normal;cursor:pointer}.control:hover{background-color:darkgrey;color:white}

